	module.exports = {"data":{"markdownRemark":{"html":"<h2>How to Create an HTML Button That Acts Like a Link</h2>\n<p>Sometimes you may want to use a button to link to another page or website rather than to submit a form or something like that. This is fairly simple to do and can be achieved in several ways.</p>\n<p>One way is to simply wrap your <code>&#x3C;button></code> tag in an <code>&#x3C;a></code> tag:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>a</span> <span class=\"token attr-name\">href</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">'</span>https://www.freecodecamp.org/<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>button</span><span class=\"token punctuation\">></span></span>Link To freeCodeCamp<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>button</span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>a</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>This transforms your entire button into a link. </p>\n<p>A second option is to create your link as you normally would with your <code>&#x3C;a></code> tag and then style it via CSS:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>a</span> <span class=\"token attr-name\">href</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">'</span>https://www.freecodecamp.org/<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">></span></span>Link To freeCodeCamp<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>a</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>Once you’ve created your link, you can the use CSS to make it look like a button. For instance, you could add a border, a background color, some styles for when the user is hovering the link…</p>\n<p>Another way to add a button is to wrap an <code>input</code> inside <code>form</code> tags. Specify the desired target URL in the form action attribute.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>form</span> <span class=\"token attr-name\">action</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>http://google.com<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>input</span> <span class=\"token attr-name\">type</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>submit<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">value</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>Go to Google<span class=\"token punctuation\">\"</span></span> <span class=\"token punctuation\">/></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>form</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<ul>\n<li><a href=\"https://guide.freecodecamp.org/css/css-buttons/\">FreeCodeCamp Guide - styling buttons</a></li>\n<li><a href=\"https://stackoverflow.com/questions/2906582/how-to-create-an-html-button-that-acts-like-a-link\">How to create an HTML button that acts like a link?</a></li>\n</ul>","fields":{"slug":"/html/tutorials/how-to-create-an-html-button-that-acts-like-a-link/"},"frontmatter":{"title":"How to Create an HTML Button That Acts Like a Link"}}},"pathContext":{"slug":"/html/tutorials/how-to-create-an-html-button-that-acts-like-a-link/"}}

